<template>
    <div>
        <b-row class="px-5 pt-3 pb-3" id="pilas-form-admin">                        
            <b-col class="bg-white p-4 col-12 mb-4 adminPila" >   
                <b-row>
                    <b-col cols="1" class="pr-0 pt-1"><div class="square1" style="background-color: #9d3eff;"></div></b-col>
                    <b-col class="pl-3 pr-5">
                        <p class="stylingTitle pb-2"> Datos generales <img :src="icono" @click="showBtn= !showBtn" class="icon float-right pt-1" alt="Edit icon"> </p>
                        
                        <form @submit.prevent="submit(base, dias_sucios, extra_m3)" >     
                            <div class="input-group mb-3">                                
                                <label class="labelB0" for="inputB0">Base 0</label>
                                <b-form-input 
                                    v-model="base"                                     
                                    required
                                    :class="['form-control',{'is-invalid' : !base && inputOnFocus, 'is-valid' : base}]" 
                                    @blur="inputOnFocus = true"
                                    id="inputB0" 
                                    type="number" 
                                    :placeholder="base0 | stringify"/>
                            </div>
                            <div class="input-group mb-3">
                                <label class="labelDS" for="inputDS">Dias sucios</label>
                                <b-form-input
                                    v-model="dias_sucios"                                    
                                    required
                                    :class="['form-control',{'is-invalid' : !dias_sucios && inputOnFocus, 'is-valid' : dias_sucios}]" 
                                    @blur="inputOnFocus = true"
                                    id="inputDS" 
                                    type="number" 
                                    :placeholder="diasS"/>    
                            </div>
                            <div class="input-group mb-3">
                                <label class="mr-5" for="inputE">$ Extra por m3</label>
                                <b-form-input  
                                    v-model="extra_m3"                                 
                                    required
                                    :class="['form-control', {'is-invalid' : !extra_m3 && inputOnFocus, 'is-valid' : extra_m3}]" 
                                    @blur="inputOnFocus = true"
                                    id="inputE" 
                                    type="number" 
                                    step=".01"
                                    :placeholder="extra"/>
                            </div>
                            <small :class="{'text-danger' : !base, 'd-none' : !inputOnFocus || base}">
                                   * No puede haber campos vacos
                                </small>
                            <button v-if="showBtn"
                            :class="['btn btn-block', { 'saved' : isAFlag, 'blue' : isAFlag == false }]"
                            class="mt-3"
                            type="submit"><img v-if="isAFlag" :src="check" class="icon mb-1 mr-1" /> {{textButton}}
                            </button>
                        </form>

                    </b-col>                            
                </b-row>                                                     
            </b-col>
            
            <b-col class="bg-white p-4 col-6 adminPila" >
                <b-row>
                    <b-col cols="1" class="pr-0 pt-1"><div class="square1" style="background-color: #00c290;"></div></b-col>
                    <b-col class="pl-3">
                        <p class="stylingTitle">Colaboradores </p>
                        <b-row>

                            <table class="table table-borderless table-responsive">
                                <thead>
                                    <tr>
                                        <th class="pb-0 pt-0" scope="col"><strong><p class="stylingSmall" style="color: #838383;">Nombre: </p></strong></th>
                                        <th class="pb-0 pt-0" scope="col"><strong><p class="stylingSmall" style="color: #838383;">Bono al da: </p></strong></th>
                                        <th class="pb-0 pt-0" scope="col"><strong><p class="stylingSmall" style="color: #838383;">Asistencia: </p></strong></th>
                                    </tr>
                                </thead>
                                <tbody>
                                   <tr>
                                        <td class="pb-0 pt-0">
                                            <ul>
                                                <li v-for="(item, index) in equipo" :key="item.id" class="stylingSmall mb-1">
                                                    <router-link :to="link(index)">{{item.nombre || cap}} </router-link>
                                                </li>
                                            </ul>
                                        </td>
                                        <td class="pb-0 pt-0">
                                            <ul style="list-style-type: none;">
                                                <li v-for="item in bono" :key="item.id" class="stylingSmall mb-1" style="color: #fd832d;">                                            
                                                    <template v-if="item">$ {{item | rand }}</template>
                                                    <template v-else>$ 0</template>                                                    
                                                </li>
                                            </ul>
                                        </td>
                                        <td class="pb-0 pt-0">
                                            <ul style="list-style-type: none;">
                                                <li v-for="item in asistencia" :key="item.id" class="stylingSmall mb-1" style="color: #fd832d;">
                                                    {{item}}
                                                </li>
                                            </ul>
                                        </td>
                                    </tr>
                                </tbody>
                                </table>

                        </b-row>
                    </b-col>
                </b-row>                       
            </b-col>
            
        </b-row>      
    </div>
</template>


<script>
import icono from '../assets/icono_edit.png';
import check from '../assets/icono_check.png';

export default {
    name: 'FormInfoBase',
    props: {
        equipo:{
            required: true
        },
        extra:{
            required: true
        },
        diasS:{
            required: true
        },
        base0:{
            required: true
        },
        bono:{
            required: true
        },
        asistencia:{
            required: true
        }
    },
    data() {
        return {
            base: '',
            dias_sucios: '',
            extra_m3: '',
            icono: icono,
            showBtn: false,
            check: check,
            inputOnFocus: false,
            valid: false,
        }
    },
    computed: {
        isAFlag() {
            let city = this.$store.getters['admin/getName'];
            let linkcity = city.toLowerCase().normalize("NFD").replace(/[\u0300-\u036f-\s-\.]/g, "");
            return this.$store.getters[`${linkcity}/flag`];
        },
        textButton() {
            let city = this.$store.getters['admin/getName'];
            let linkcity = city.toLowerCase().normalize("NFD").replace(/[\u0300-\u036f-\s-\.]/g, "");
            return this.$store.getters[`${linkcity}/getNotif`];
        },
        depName() {
            return this.$store.getters["admin/getDep"];
        }
    },
    methods: {
        editInfo: function (base, dias_sucios, extra_m3) {
            let city = this.$store.getters['admin/getName'];
            let dep = this.depName;
            let linkdepto;
            dep == 'Chofer Local'? linkdepto = dep.toLowerCase().replace(/ /g,"-") : linkdepto = dep.toLowerCase().normalize("NFD").replace(/[\u0300-\u036f-\ ]/g, "");
            let linkcity = city.toLowerCase().normalize("NFD").replace(/[\u0300-\u036f-\s-\.]/g, "");            
            this.$store.dispatch(`${linkcity}/editInfo`, {
                base: base,
                dias_sucios: dias_sucios,
                extra_m3: extra_m3,
                depto: linkdepto
            });
        },
        wait: function(){
            setTimeout(() => this.$router.go(), 1000);
        },
        validate: function (base, dias_sucios, extra_m3) {
            this.inputOnFocus = true;
            if (base && dias_sucios && extra_m3) {
                this.valid = true;
            }
        },
        submit: function(base, dias_sucios, extra_m3) {
            this.validate(base, dias_sucios, extra_m3);
            if (this.valid) {                
                this.editInfo(base, dias_sucios, extra_m3);
                this.wait();
            }
        },
        link: function (user) {
            let city = this.$store.getters['admin/getName'];
            let dep = this.depName
            let linkcity = city.toLowerCase().normalize("NFD").replace(/[\u0300-\u036f-\s-\.]/g, "");
            let linkdepto;
            dep == 'Chofer Local'? linkdepto = dep.toLowerCase().replace(/ /g,"-") : linkdepto = dep.toLowerCase().normalize("NFD").replace(/[\u0300-\u036f-\ ]/g, "");
            return '/'+linkcity+'/'+linkdepto+'/'+user ;
        }
    },
    filters:{
        rand: (value) => {
            if (!value) return '';
            if (typeof value === 'number') {
                return value.toFixed(2);
            }
            return value;
        },
        stringify: (value) => {
            if(!value) return '';
            if (typeof value == 'number'){
                return value.toString();
            }
        },
        cap: (value)=>{
            if (!value) return '';
            value = value.toString();
            let splitStr = value.toLowerCase().split(' ');
            let len = splitStr.length
            let i; 
            for (i = 0; i < len; i++) {
                splitStr[i] = splitStr[i].charAt(0).toUpperCase() + splitStr[i].substring(1);     
            }
            let nameCap = splitStr.join(' '); 
            return nameCap;
        }
    }
}
</script>
