<template>
    <div>
        <div class="row mx-0">
            <div class="col-sm-12 col-md-3 px-0">
                <Header
                :name="user"
                />

                <SideMenu/>
            </div>
            <div class="col-sm-12 col-md-9 bg-gris">
                <b-row class="py-4 px-5">
                    <b-col cols="12" md="6">
                        <h2 class="blueText">{{cityName}}</h2>
                    </b-col>
                    <b-col cols="12" md="6">
                        <Dropdown
                        :departments="dep.deptos"
                        />
                    </b-col>
                </b-row>
                <hr>
                <b-row>
                    <b-col cols="12" md="7" >
                        <div v-if="isLoading" class="center pt-5">
                            <div class="spinner-border text-primary" role="status">
                                <span class="sr-only">Loading...</span>
                            </div>
                        </div>
                        <FormInfoBase
                        :equipo="bd.equipo"
                        :extra="bd.$_extra_m3"
                        :diasS="bd.dias_sucios"
                        :base0="bd.base0"
                        :bono="depto.bono_persona"
                        :asistencia="depto.asistencia"
                        />
                    </b-col>
                    <b-col cols="12" md="5" class="px-0">
                        <Graphics/>
                    </b-col>
                </b-row>
                
            </div>
        </div>
        
    </div>
</template>


<script>
import Header from '../components/Header';
import SideMenu from '../components/SideMenu';
import FormInfoBase from '../components/FormInfoBase';
import Graphics from '../components/Graphics';
import Dropdown from '../components/Dropdown';

export default {
    name: 'Dashboard',
    components: {
        Header,
        SideMenu,
        FormInfoBase,
        Graphics,
        Dropdown
    },
    data() {
        return {
            user: 'Enrique Uriegas',
        }
    },
    created() {
        this.$store.dispatch("lapaz/getBDInfo", 'almacen');
        this.$store.dispatch("lapaz/getAllInfo", 'almacen');
        this.$store.dispatch('admin/getInfo', 'La Paz');
    },
    computed: {
        isLoading() {
            return this.$store.getters[`${this.normalize(this.cityName)}/loading`];
        },
        depto() {
            return this.$store.getters[`${this.normalize(this.cityName)}/data`];
        },
        bd() {
            return this.$store.getters[`${this.normalize(this.cityName)}/bd`];
        },
        cityName() {
            return this.$store.getters["admin/getName"];
        },
        depName() {
            return this.$store.getters["admin/getDep"];
        },
        dep(){
            return this.$store.getters['admin/data'];
        }
    },
    methods: {
        normalize: function (city) {
            let nCity = city.toLowerCase().normalize("NFD").replace(/[\u0300-\u036f-\s-\.]/g, "");
            return nCity;
        }
    },

}
</script>
<style scoped>
hr{
    border-color: white;
}
.blueText{
    color: #005cc3;
    font-family: 'FuturaPTHeavy';
}

.bg-gris{
    border-radius: 0;
}


</style>